<template>
  <div class="goods">
    <van-row class="left_menu" @click="onClickLeft">
      <van-icon name="arrow-left" />
    </van-row>
    <van-swipe class="goods-swipe" :autoplay="3000">
      <van-swipe-item v-for="thumb in goods.thumb" :key="thumb">
        <img :src="thumb" width="100%">
      </van-swipe-item>
    </van-swipe>

    <van-cell-group>
      <van-cell>
        <div class="goods-title">{{ goods.title }}</div>
        <div class="goods-price">{{ formatPrice(goods.price) }}</div>
      </van-cell>
      <van-cell class="goods-express">
        <van-col span="10">运费：{{ goods.express }}</van-col>
        <van-col span="14">剩余：{{ goods.remain }}</van-col>
      </van-cell>
    </van-cell-group>

    <van-cell-group class="goods-cell-group" @click="buy(1)">
      <van-cell value="" icon="shop-o" is-link @click="sorry">
        <template #title>
          <span class="van-cell-text">小米11 Pro 8GB+256GB 黑色</span>
        </template>
      </van-cell>

    </van-cell-group>

    <van-cell-group class="goods-cell-group">
      <van-cell value="进入店铺" icon="shop-o" is-link @click="sorry">
        <template #title>
          <span class="van-cell-text">有赞的店</span>
          <van-tag class="goods-tag" type="danger">官方</van-tag>
        </template>
      </van-cell>
      <van-cell title="线下门店" icon="location-o" is-link @click="sorry"/>
    </van-cell-group>

    <van-cell-group class="goods-cell-group">
      <van-cell title="查看商品详情" is-link @click="sorry"/>
    </van-cell-group>

    <van-tabs v-model="active" style="min-height:400px;">
      <van-tab title="商品详情">
        <!--html-->
        <van-empty v-if="content==''" image="search" description="暂无详情" />
        <van-row v-else>
          {{content}}
        </van-row>
      </van-tab>


      <van-tab title="商品参数">
        <van-empty v-if="content==''" image="error" description="暂无详情" />
        <van-row v-else>
          {{can}}
        </van-row>

      </van-tab>


      <van-tab title="买家评论">
        <van-empty v-if="pinglun==''" image="search" description="暂无详情" />
        <van-row v-else>
          {{pinglun}}
        </van-row>
      </van-tab>

    </van-tabs>

    <van-goods-action>
      <van-goods-action-icon icon="chat-o" @click="sorry">
        客服
      </van-goods-action-icon>
      <van-goods-action-icon icon="cart-o" @click="onClickCart">
        购物车
      </van-goods-action-icon>
      <van-goods-action-button type="warning" @click="buy(1)">
        加入购物车
      </van-goods-action-button>
      <van-goods-action-button type="danger" @click="buy(2)">
        立即购买
      </van-goods-action-button>
    </van-goods-action>

    <!--商品规格-->
    <van-sku v-model="show" :sku="sku" :goods="goodstype" :goods-id="goodsId" :quota="quota" :quota-used="quotaUsed"
             :hide-stock="sku.hide_stock" :message-config="messageConfig" @buy-clicked="onBuyClicked"
             @add-cart="onAddCartClicked"/>
  </div>
</template>

<script>


export default {
  name: 'goods',
  data() {
    return {
      active: 0,
      content: '',
      can: '',
      pinglun: '',
      show: false,
      sku: {
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [{
          k: '颜色', // skuKeyName：规格类目名称
          k_s: 's1', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
          v: [{
            id: '1', // skuValueId：规格值 id
            name: '红色', // skuValueName：规格值名称
            imgUrl: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/d3a36269683536a9d46a044392ed37ab.png', // 规格类目图片，只有第一个规格类目可以定义图片
            previewImgUrl: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/d3a36269683536a9d46a044392ed37ab.png', // 用于预览显示的规格类目图片
          },
            {
              id: '2',
              name: '蓝色',
              imgUrl: 'https://img01.yzcdn.cn/2.jpg',
              previewImgUrl: 'https://img01.yzcdn.cn/2p.jpg',
            }
          ],
          largeImageMode: true, //  是否展示大图模式
        }],
        // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
        list: [{
          id: 2259, // skuId
          s1: '1', // 规格类目 k_s 为 s1 的对应规格值 id
          s2: '2', // 规格类目 k_s 为 s2 的对应规格值 id
          price: 100, // 价格（单位分）
          stock_num: 110 // 当前 sku 组合对应的库存
        }],
        price: '1.00', // 默认价格（单位元）
        stock_num: 227, // 商品总库存
        collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
        none_sku: false, // 是否无规格商品
        messages: [{
          // 商品留言
          datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
          multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
          name: '留言', // 留言名称
          type: 'text', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
          required: '1', // 是否必填 '1' 表示必填
          placeholder: '' // 可选值，占位文本
        }],
        hide_stock: false // 是否隐藏剩余库存
      },
      goodstype: {
        // 数据结构见下方文档
        picture: 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/d3a36269683536a9d46a044392ed37ab.png'
      },
      messageConfig: {},
      goodsId: 1,
      quota: 0,
      quotaUsed: 0,
      goods: {
        title: '美国伽力果（约680g/3个）',
        price: 2680,
        express: '免运费',
        remain: 19,
        thumb: [
          'https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg',
          'https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg'
        ]
      }
    };
  },

  methods: {
    formatPrice() {
      return '¥' + (this.goods.price / 100).toFixed(2);
    },
    onBuyClicked() {
      this.show=false;
      this.$router.push('addOrder')
    },
    onAddCartClicked() {

    },
    onClickCart() {
      this.$router.push('cart');
    },

    buy(id) {
      this.show = true;
    },

    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },


    sorry() {
      this.$toast('暂无后续逻辑~');
    }
  }
};
</script>

<style>
.left_menu {
  position: fixed;
  z-index: 1000;
  top: 10px;
  left: 10px;
}

.left_menu .van-icon {
  padding: 5px;
  background: #DDDDDD;
  border-radius: 50%;
  font-size: 20px;
}

img {
  max-width: 100%;
}
</style>



